// SECTION WHERE THE USER IS ASKED TO JOIN OR SIGN IN
.join-box{
  margin-left:5%;
  margin-right: 5%;
  // width:1200px;
  // height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.join {
  width: 75%;
  height: 290px;
  background: rgba(238, 234, 233, 0.611);
  backdrop-filter: blur( 4px);
-webkit-backdrop-filter: blur( 15px );
  opacity: .92;
  border-radius: 2px;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(81, 126, 128, 0.45) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

