}

p{
  text-align: center;
  margin-top: 10px;
  margin-bottom: 40px;
  color:black;
}

.buttons{
  display: flex;
  justify-content:space-evenly;
}

button.ui.button.join-button{
  width: 150px;
  background-color: #1190CB;
  border-radius: 1px;
  color:white;
  font-weight: bold;
}

button.ui.button.join-button:hover{
  background-color: rgba(212, 36, 109, 0.8);
  transition: .5s;
}

