  border-radius: 1px;
  border: 1.5px solid #1190CB;
  font-weight: bold;
}
button.ui.button.sign-button:hover{
  background-color: rgba(212, 36, 109, 0.8);
  color:white;
  transition: .5s;
}


// SECTION PHILOSOPHY
.philosophy{
  width: 100%;
  height:750px;
  background-color: aliceblue;
  background-image:linear-gradient((to bottom, rgba(223, 251, 255, 0.2), rgba(253, 253, 253, 0.73)))
