  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 14px 20px -10px rgba(58, 57, 55, 0.7);
}

.learn-more {
  border-radius: 0;
  width: 150px;
  background-color: #f1a80b;
  border:none;
  color:white;

}

.learn-more:hover{
  background-color: rgba(212, 36, 109, 0.8);
  transition: .5s;
  color:white;
}

button.ui.button.learn-more{
  margin-left: 30%; 
}

// SECTION WHY CRUMBLE
.why-crumble{
  width: 100%;
  height:750px;
}

.crumble-container{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  
}
.left-crumble-box{
  width: 750px;
  height: 650px;
  background-image:linear-gradient((to top right, rgb(253, 153, 4), rgba(224, 193, 54, 0.6)));
  border-radius: 10px;
  // position: inherit;
}

.first-message, .third-message{
  font-size: 1.3rem;
  width: 360px;
  height: 70px;
  text-align:center;
  background-color: orange;
  border-radius: 25px 25px 0 25px;
  margin-left: 200px;
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.second-message, .fourth-message{
  font-size: 1.3rem;
  width: 260px;
  height: 70px;
  text-align:center;
  vertical-align: center;
  background-color: white;
  border-radius: 25px 25px 25px 0;
  margin-left: 80px;
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.why{
  width:700px;
  height:260px;
  margin-top: 140px;
  background-color: white;
  margin-left: -130px;
  box-shadow: 0 14px 20px -10px rgba(58, 57, 55, 0.7);
  border-radius: 10px;
}

.text-why h3, p, .why-button{
  text-align: left;
  margin-left: 20px;
  margin-top: 20px;
}
button.ui.button.why-button{
  margin-left: 20px;
}
.why-button{
  border-radius: 0;
  width: 150px;
  background-color: #f1a80b;
  border:none;
  color:white;
}
.why-button:hover, .why-button:focus{
  
  background-color: rgba(212, 36, 109, 0.8);
  transition: .5s;
  color:white;
  animation:pulse 1s;
  box-shadow: 0 0 0 1em rgba(#fff, 0)
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 var(--hover); }
}
.why-button {  
  color: var(--color);
  transition: 0.25s;
  &:hover,
  &:focus { 
    border-color: var(--hover);
    color: #fff;
  }
}

